import React, { useState, useEffect } from 'react'
import { Form, Input, Button, Select, Card, Row, Col, Divider, Space } from 'antd';
import Api from '../../helpers/api';
import { message } from "antd";
import {
    ReloadOutlined,
    ArrowLeftOutlined,
    SaveOutlined
} from '@ant-design/icons';
import handleApiError from '../../helpers/handleApiError';

const api = new Api();
const { Option } = Select;

const FormPage = (props) => {

    const [errors, setErrors] = useState({})
    const [loading, setLoading] = useState(false)
    const [plant, setPlant] = useState([])
    const [user, setUser] = useState([])
    const [form] = Form.useForm();

    useEffect(() => {
        const { add, edit } = props

        if (edit) {
            getEditData()
        }

        fetchPlant()
        fetchUser()
    }, []);

    const onFinish = (data) => {
        setLoading(true)

        let apiPost
        //handling if form add
        if (props.add) {
            apiPost = api.storeRoom(data)
        } else if (props.edit) {
            //handling if form edit
            const { id } = props.match.params
            apiPost = api.updateRoom(id, data)
        }

        apiPost.then(response => {
            console.log(response)
            setLoading(false)
            // console.log(response.data);
            message.success('save data success');
            props.history.goBack();
        }).catch(error => {
            setLoading(false)
            if (error.response && error.response.data) {
                const { errors } = error.response.data
                setErrors(errors)
                // message.error(message);
            }
            handleApiError(error, props)
        });
    };

    const handleSearchPlant = (value) => {
        fetchPlant(value)
    }

    const handleChangePlant = () => {
        fetchPlant('')
    }

    const fetchPlant = (q) => {
        api.getPlant({ q: q }).then(response => {
            const { data } = response.data;
            setPlant(data);
        }).catch(err => {
            console.log(err)
            handleApiError(err, props)
        })
    };

    const handleSearchUser = (value) => {
        fetchUser(value)
    }

    const handleChangeUser = () => {
        fetchUser('')
    }

    const fetchUser = (q) => {
        api.getAllUsers({ q: q }).then(response => {
            const { data } = response.data;
            setUser(data);
        }).catch(err => {
            console.log(err)
            handleApiError(err, props)
        })
    };

    const getEditData = () => {
        const { id } = props.match.params

        api.viewRoom(id)
            .then(response => {
                const { code, name, plant_id, responsible_person, description } = response.data
                form.setFieldsValue({
                    code,
                    name,
                    plant_id,
                    responsible_person,
                    description,
                });
            }).catch(error => {
                handleApiError(error, props)
            })
    }

    const handleReset = () => {
        form.setFieldsValue({
            code: '',
            name: '',
            description: '',
            plant_id: '',
            responsible_person: '',
        });
    }

    return (

        <Card>
            <Form name="complex-form" onFinish={onFinish} labelCol={{ span: 4 }} wrapperCol={{ span: 8 }}
                form={form}>

                <Form.Item name="code" label="Code"
                    validateStatus={errors && errors.code ? 'error' : undefined}
                    help={errors ? errors.code : null}
                    rules={[{ required: true, message: 'Please input code' }]}>
                    <Input />
                </Form.Item>

                <Form.Item name="name" label="Name"
                    validateStatus={errors && errors.name ? 'error' : undefined}
                    help={errors ? errors.name : null}
                    rules={[{ required: true, message: 'Please input name' }]}>
                    <Input />
                </Form.Item>

                <Form.Item name="plant_id" label="Plant"
                    validateStatus={errors && errors.plant_id ? 'error' : undefined}
                    help={errors ? errors.plant_id : null}
                    rules={[{ required: true, message: 'Please input plant' }]}>
                    <Select
                        placeholder="Select"
                        filterOption={false}
                        showSearch
                        onSearch={handleSearchPlant}
                        onChange={handleChangePlant}
                        name="plant_id">

                        {plant.map(item => (
                            <Option key={item.id} value={item.id}>{item.name}</Option>
                        ))}
                    </Select>
                </Form.Item>

                <Form.Item name="responsible_person" label="Responsible Person"
                    validateStatus={errors && errors.responsible_person ? 'error' : undefined}
                    help={errors ? errors.responsible_person : null}
                    rules={[{ required: true, message: 'Please input responsible person' }]}>
                    <Select
                        placeholder="Select"
                        filterOption={false}
                        showSearch
                        onSearch={handleSearchUser}
                        onChange={handleChangeUser}
                        name="responsible_person">

                        {user.map(item => (
                            <Option key={item.id} value={item.id}>{item.email}</Option>
                        ))}
                    </Select>
                </Form.Item>

                <Form.Item name="description" label="Description"
                    validateStatus={errors && errors.desctiption ? 'error' : undefined}
                    help={errors ? errors.desctiption : null}
                    rules={[{ required: true, message: 'Please input desctiption' }]}>
                    <Input.TextArea />
                </Form.Item>

                <p><span style={{ color: '#f5222d', fontSize: 14 }}>*</span> Required</p>
                <Divider />

                <Space>
                    <Button onClick={() => props.history.goBack()}
                        icon={<ArrowLeftOutlined />}
                    >Back</Button>

                    <Button onClick={() => handleReset()}
                        icon={<ReloadOutlined />}
                    >Reset</Button>

                    <Button type="primary" htmlType="submit" loading={loading}
                        icon={<SaveOutlined />}
                    >Save</Button>
                </Space>

            </Form>
        </Card>
    )
}

export default FormPage;